body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#282c34}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.btn{background-color:yellowgreen;color:white;border:3px solid green;border-radius:3px;padding:5px;font-weight:800;font-size:10pt}.btn-danger{background-color:orangered;border:3px solid darkred}.container{margin:auto;min-width:200px}.add-button{position:absolute;top:0px;right:10px;text-align:center;width:32px;font-size:xx-small}#add-icon{width:32px;height:32px;border:1px solid yellowgreen;background-color:white;border-radius:8px}.version{font-size:x-small;font-style:italic;width:50%;min-width:350px;margin:auto;color:grey;text-align:left}

.settings-container{width:50%;min-width:160px}.settings-input{padding:8px}.settings-label{padding-bottom:2px}.settings-buttons{display:flex}.settings-buttons .btn{margin-right:5px;white-space:nowrap}

.history-panel-sessions{display:grid;grid-template-columns:100px auto auto auto auto;border:1px solid grey}.history-panel-sessions .header{border-bottom:1px solid grey}.history-panel-sessions .col1,.history-panel-sessions .col2,.history-panel-sessions .col3,.history-panel-sessions .col4{border-right:1px solid grey}.history-panel-sessions .col2,.history-panel-sessions .col3,.history-panel-sessions .col4,.history-panel-sessions .col5{padding-left:5px;padding-right:5px}.history-panel-buttons{padding-top:10px;font-size:1em;font-family:sans-serif}.delete-history-button{float:right}.export-button{margin-right:4px;margin-bottom:4px}.history-panel-buttons .import-label{cursor:default;font-weight:800;font-size:10pt;white-space:nowrap;padding:5px;margin-bottom:4px}#upload-file{opacity:0;position:absolute;z-index:-1}.history-panel-buttons-import-export{display:flex;flex-wrap:wrap}

.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.9)}.modal-main{position:fixed;width:80%;height:auto;top:50%;left:50%;transform:translate(-50%, -50%)}.modal-display-block{display:block}.modal-display-none{display:none}.modal-buttons{float:right}

.btn-x{background-color:orangered;border:3px solid darkred;color:white;border-radius:3px;padding:0px 5px;margin-left:5px;font-weight:800}

.add-drink,.drinks,.stats{padding-top:15px}.add-drink-button{padding-top:5px}.active-session-buttons{padding-top:15px}.active-session-buttons button{margin-right:6px}.active-session-field-value{margin-left:10px}.active-session-warning{color:red}.active-session-warning-icon{margin-left:5px;cursor:default}

.modal-true-false{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.9)}.modal-true-false-main{position:fixed;width:80%;height:auto;top:50%;left:50%;transform:translate(-50%, -50%)}.modal-true-false-display-block{display:block}.modal-true-false-display-none{display:none}.modal-true-false-buttons{float:right;display:flex}.modal-true-false-buttons-spacer{width:3px}

.disclaimer-main{width:80%;margin:auto;background-color:black;border:1px solid white;border-radius:5px;padding:8px}.disclaimer-content{text-align:left}.disclaimer-buttons{text-align:right}a{color:white}

